import { Company } from './company';

export class PersonBodyUpdate {
    firstName?: string;
    lastName?: string;
    description?: string;
    email?: string;
    skypeId?: string;
    googleId?: string;
    instantMessageId?: string;
    jobTitle?: string;
    location?: string;
    company?: Company;
    mobile?: string;
    telephone?: string;
    userStatus?: string;
    enabled?: boolean;
    emailNotificationsEnabled?: boolean;
    password?: string;
    oldPassword?: string;
    aspectNames?: string[];
    properties?: any;

    constructor(input?: any) {
        if (input) {
            Object.assign(this, input);
            this.company = input.company ? new Company(input.company) : undefined;
        }
    }

}
